<template>
  <div>
    <detail v-bind="info" />
  </div>
</template>

<script>

import detail from "../components/detail.vue";
import empresa from "../assets/empresa.json";
import listado from "../assets/listado.json";
import services from "../services.js";
console.dir(services);

let getAnuncio = (dir) => require(`../assets/${dir}/anuncio.json`);
let getSfotos = (dir) => services.split(listado.filter(l=>l.path === dir)[0].images);

export default {
  name: "anuncioDetail",
  computed: {
    info() {
      var dir = this.$route.params.dir;
      let anuncio = getAnuncio(dir);
      let sfotos = getSfotos(dir);
      return {
        anuncio,
        empresa,
        dir,
        sfotos
      };
    }
  },
  components: {    
    detail
  }
};
</script>

