import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'

import frontView from "./views/front.vue";
import anuncioDetailView from "./views/anuncioDetail.vue";
import notFoundView from "./views/notFound.vue";

Vue.config.productionTip = false

const routes = [
  { path: '/', redirect: '/anuncios' }
  ,{ path: '/anuncios', name: 'anuncios', component: frontView }
  ,{ 
    path: '/anuncios/:dir', 
    name: 'anuncio-detail', 
    component: anuncioDetailView
  }
  ,{ path: '*', component: notFoundView },
]

Vue.use(VueRouter);

const router = new VueRouter({
  routes
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
