<template>
  <div>
    <company v-bind="empresa" />
    <products v-bind="anuncios" />
  </div>
</template>

<script>
import company from "../components/company.vue";
import products from "../components/products.vue";

import empresa from "../assets/empresa.json";
import listado from "../assets/listado.json";
import services from "../services.js";
console.dir(services);

var anuncios = listado.map(dir => {
  var anuncio = require(`../assets/${dir.path}/anuncio.json`);
  return {
    anuncio: anuncio,
    dir: dir
  };
});

var splitted = services.split(anuncios);

export default {
  name: "front",
  components: {    
    company,
    products
  },
  data: () => {
    return {
      empresa: empresa,
      anuncios: {
        splitted: splitted
      }
    };
  }
};
</script>

