
export default {
    split: (list) => {
        let split = [];
        for (let i = 0; i < list.length; i++) {
            if (i === 0 || i % 3 === 0) {
                split.push([]);
            }

            let arr = split[split.length - 1];
            arr.push(list[i]);
        }

        return split;
    },
    getJson: (url, callback) => {
        var oReq = new XMLHttpRequest();
        oReq.onload = function () {
            let data = JSON.parse(oReq.responseText);
            callback(data);
        };
        oReq.open("get", url, true);
        oReq.send();
        return oReq;
    }
};

